html, body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  background: #000;
  color: #fff;
  height: 100%;
}

.pageHollder {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
  margin-bottom: 20px;
  align-items: center; 
  text-align: center; 
  color: #fff; 
  border: 3px solid #000000a9; 
  border-radius: 20px;
  margin: 20px;
  padding: 20px; /* Adjust the padding as needed */
  width: 100%;
  box-sizing: border-box;
  overflow-x: hidden;
  overflow-y: hidden;
  background: #303030;      
}

.title-Box {
  overflow-x: hidden;
  overflow-y: hidden;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  text-align: center;
  width: 100%;
}

.title-bar {
  box-sizing: border-box;
  overflow-x: hidden;
  overflow-y: hidden;
  display: flex;
  flex-direction: row;
  background-color: #303030;
  align-items: center; 
  text-align: center; 
  padding: 20px;
  height: 100px;
  width: 100%; 
  border: 5px solid #202020; 
}

.title {
  overflow-x: hidden;
  overflow-y: hidden;
  color: #888;
  margin: 0;
  padding: 0;
  font-size: 24px;
}

.author {
  overflow-x: hidden;
  overflow-y: hidden;
  margin-bottom: 0;
  padding: 10;
  font-size: 14px;
  color: #888;
}

.timer {
  overflow: hidden;
  margin-bottom: 0;
  margin-left: 500px;
  font-size: 25px;
  color: #ff0000;
}

.logo {
  padding: 10px;
}

.CenterCharts {
  overflow-x: hidden;
  overflow-y: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  padding: 20px;
  width: 100%;
}

.chart-grid {
  box-sizing: border-box;
  display: flex;
  flex: 1; /* Let it take up all available space horizontally */
  flex-direction: column;
  align-items: center;
  gap: 10px;
  padding: 10px;
  width: 100%;
}

.chart-row {
  box-sizing: border-box;
  overflow-x: hidden;
  overflow-y: hidden;
  display: flex;
  flex-wrap: wrap; /* Allow charts to wrap to the next row */
  justify-content: space-between;
  gap: 20px;
}

.chart {
  flex: 1; /* Let the chart take up all available space in the row */
  background: #5a5a5a;
  color: #fff;
  border: 3px solid #000000a9;
  padding: 10px;
  box-sizing: border-box;
}

.chart-title {
  overflow-x: hidden;
  overflow-y: hidden;
  background: #000000; 
  color: #ffffff; 
}

.chart-label {
  overflow-x: hidden;
  overflow-y: hidden;
  color: #ffffff; 
}

.player-card-holder {
  display: flex;
  flex-wrap: wrap;
  background:#5a5a5a;
  justify-content: space-evenly;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
  margin-bottom: 20px;
  align-items: center; 
  text-align: center; 
  color: #fff; 
  border: 3px solid #000000a9; 
  border-radius: 20px;
  margin: 20px;
  padding: 20px; /* Adjust the padding as needed */
  width: 100%;
}